body {
    background-color: #333333;
    height: 100vh;
    overflow-x: hidden;
    font-family: 'IBM Plex Sans', sans-serif;
}

#iframe {
    width: 21cm;
    left: calc(50% - 15.75cm);
    position: absolute;
    overflow: visible;
    border: 0;
    padding: 0;
    animation: 0.15s fade;
}

@keyframes fade {
    0% {
        opacity: 0;
    } 100% {
        opacity: 1;
    }
}

::-moz-selection {
    background-color: rgba(68, 136, 255, 0.25);
}

#open {
    visibility: hidden;
}

#options {
    padding: 8px;
    display: inline-block;
    position: absolute;
    z-index: 1;
    pointer-events: none;
    opacity: 0;
    left: calc(50vw + 5.25cm);
    width: 10.5cm;
    transition: top 0.15s, opacity 0.15s;
}

#options.focus {
    pointer-events: all;
    opacity: 1;
}

#first, #second {
    display: block;
    font-family: inherit;
    font-size: 12pt;
    padding: 8px;
    border: 2px solid white;
    outline: none;
    margin: 0;
    color: white;
    position: relative;
    word-break: break-word;
    white-space: pre-wrap;
    transition: border-color 0.15s;
    border-radius: 8px;
}

#first:focus, #second:focus {
    border-color: #4488FF;
}

#second.error {
    border-color: #FF4444;
}

#second {
    margin-top: 12px;
}

#second[data-placeholder="undefined"] {
    display: none;
}

#first::before, #second::before {
    content: attr(data-placeholder);
    position: absolute;
    padding: 2px;
    transform: translateY(-100%);
    font-size: 9pt;
    background-color: #333333;
}

#second[data-placeholder="text"] + #display {
    display: none;
}

#display {
    -moz-appearance: none;
}

#display + span {
    position: absolute;
    top: 0;
    z-index: 1;
}

#display:checked + span {
    text-align: center;
}

#display + span:after {
    padding: 2px;
    background-color: #333333;
}

#display:checked + span::after {
    content: 'display';
}

#display:not(:checked) + span::after {
    content: 'inline';
}

#label {
    color: white;
    font-size: 9pt;
    position: relative;
    display: block;
    margin-top: 12px;
}

#display + span + div {
    border: 1px solid white;
    width: 100%;
    display: block;
    content: '';
    position: absolute;
    width: 100%;
    top: 50%;
    transform: translateY(-50%);
    transition: border-color 0.15s;
    border-radius: 8px;
}

#display:focus + span + div {
    border-color: #4488FF;
}

#files div {
    background-color: white;
    margin: 1em;
    font-family: 'georgia', serif;
    color: #333333;
}

#files a {
    text-decoration: inherit;
    color: inherit;
}

#keyboard {
    height: 43.75vw;
    width: 100vw;
    padding-top: calc(50vh - 21.875vw);
    padding-bottom: calc(50vh - 21.875vw);
    overflow: hidden;
    background-color: white;
    opacity: 0;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 2;
    pointer-events: none;
    transition: opacity 0.15s;
}

#keyboard.keyboard {
    opacity: 1;
    pointer-events: all;
}

#keyboard > input {
    height: 0;
    width: 0;
    -moz-appearance: none;
    border: none;
}

.row {
    width: 100%;
    height: 16%;
    margin-left: 1vw;
    position: relative;
}

.key {
    font-size: 1.2vw;
    display: inline-block;
    width: 7%;
    height: 100%;
    text-align: center;
    vertical-align: middle;
    background-color: #333333;
    color: white;
    border-radius: 1.2vw;
    position: relative;
    transition: opacity 0.15s;
    opacity: 1;
    border: 0.3vw solid white;
    box-sizing: border-box;
    transition: transform 0.05s;
}

.key.down {
    transform: translateY(0.7vw);
}

#s, #o, #p {
    background-color: #AA44FF;
}

#\30, #\31, #\32, #\33 {
    background-color: #FF8844;
}

#g, #h, #j, #m {
    background-color: #FF4444;
}

#b, #i, #l {
    background-color: #00BB66;
}

#a, #z, #x, #c, #v, #f, #k, #return {
    background-color: #4488FF;
}

#caps\ lock, #return {
    width: 10.5%;
}

[id^='shift'] {
    width: 14%;
}

#keyboard [id^='shift'].key::after {
    content: '⇧';
}

#keyboard [id^='command'].key::after {
    content: '⌘';
}

#keyboard [id^='control'].key::after {
    content: '⌃';
}

#keyboard [id^='option'].key::after {
    content: '⌥';
}

#caps\ lock::after {
    content: '⇪';
}

#return::after {
    content: '⏎'
}

#delete::after {
    content: '⌫';
}

#tab::after {
    content: '⇥';
}

#esc::after {
    content: '⎋';
}

#\  {
    width: 35%;
}

.key::after {
    content: attr(id);
    top: 50%;
    position: absolute;
    transform: translate(-50%, -50%)
}

#left::after {
    content: '←';
}

#right::after {
    content: '→';
}

#up {
    height: 50%;
    border-bottom: 1px solid white;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    top: -25%;
}

#down {
    height: 50%;
    bottom: -25%;
    border-top: 1px solid white;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    left: -7%;
}

#up::after {
    content: '↑';
}

#down::after {
    content: '↓';
}

#right {
    position: absolute;
    left: 91%;
    bottom: 0;
}

.shift .key::after {
    content: attr(data-shift);
}

.command .key::after {
    content: attr(data-command);
}

.shift.command .key::after {
    content: attr(data-both);
}

#empty {
    visibility: hidden;
}

.row:first-child .key:not(#esc) {
    background-color: transparent;
    color: #333333;
}